import { getArticleList, getArticleDetail } from '@/api/biunique'

export default {
  data () {
    return {
      list:{},
      listData:[],
      visible:false,
      articleDetail:null,
      pagination:{
        current:1,
        pageSize:10
      },
      loading: false,
      finished: false,
    }
  },
  computed:{
    titleName() {
      switch (+this.$route.params.id){
      case 1 : return '理科'
      case 2 : return '工科'
      case 3 : return '商科'
      case 4 : return '文科'
      default: return '理科'
      }
    }
  },
  // mounted(){
  //   this.getListHand()
  // },
  methods:{
    async getListHand() {
      const res = {
        page: this.pagination.current,
        pageSize: this.pagination.pageSize
      }
      const { data:{code, data:{entries, total} } } = await getArticleList({categoryId:+this.$route.params.id, res})
      if (code === 0 ){
        this.loading = false
        let obj = {}
        this.listData =  this.listData.concat(entries) 
        this.listData.forEach(element => {
          if (!obj[element.categoryName]) {
            obj[element.categoryName] = [element]
          } else {
            obj[element.categoryName].push(element)
          }
        })
        this.pagination.current = this.pagination.current + 1
        if (this.listData.length === +(total)) {
          this.finished = true
          this.loading = false
        }
        this.list = obj
      }
    },
    async handleClick(id) {
      //先阻止弹出详情
      return false
      const { data } = await getArticleDetail(id)
      if (data.code === 0) {
        this.articleDetail = data.data.content
        this.visible = true
      }
    },
    onLoad(){
      setTimeout(() =>{
        this.getListHand()
      }, 500)
    }
  },
  head () {
    return {
      title: '科研背景提升'
    }
  }
}
