/**一对一接口 */
import request from "@/utils/request"
import qs from "qs"

//培训订单-获取优惠券
export const getTradeCoupon = () => {
  return request.post(`/school/train/trade/coupon`)
}

//培训订单-创建订单
export const submitTrade = (params) => {
  return request.post(`/school/train/trade/submit`, params, {
    headers: {
      'Content-Type': 'application/json;charset=utf-8'
    }
  })
}

//培训文章-学科分类列表
export const getArticleList = (params) => {
  const { categoryId, res } = params
  return request.post(`/school/train/article/subject/${categoryId}`, qs.stringify(res))
}

//培训文章-学科文章详情
export const getArticleDetail = (articleId) => {
  return request.post(`/school/train/article/subject/detail/${articleId}`)
}

//一对一论文上传
export const uploadTrade = (params) => {
  return request.post(`/school/train/trade/upload`, params)
}


//培训文章-专家列表
export const getArticleExperts = () => {
  return request.post(`/school/train/article/experts`)
}

//获取一对一分类列表
export const getCategoryList = (params) => {
  const { parentId, type } = params
  return request.post(`/school/train/article/category/list/${parentId}/${type}`)
}

//获取一对一分类详情
export const getCategoryInfo = (id) => {
  return request.post(`/school/train/article/category/info/${id}`)
}

//获取一对一栏目
export const getStoreCategory = () => {
  return request.post(`/school/train/store/category`)
}

//获取一对一课程
export const getStoreStores = (params) => {
  return request.post(`/school/train/store/stores`, qs.stringify(params))
}

//获取课程详情
export const getStoreDetail = (storeId) => {
  return request.post(`/school/train/store/detail/${storeId}`)
}

//背景提升问卷
export const submitProblem = (params) => {
  return request.post(`/school/train/trade/problem`, qs.stringify(params))
}

//背景提升课程状态 
export const getStoreInfo = (storeId) => {
  return request.post(`/school/train/store/info/${storeId}`)
}
